
.background {
    background: url("images/bg.jpg");
    background-size: cover;
    padding-top: 100px;
    padding-bottom: 100px;
   }
  
   .submit {
    text-align: center;
    font-family: 'Edu SA Beginner', serif;
    font-size: 1.15em;
    background-color: #FFFFFF;
    border-radius: 25px;
    padding: 15px;
    padding-bottom: 35px;
    border-style: solid;
    border-width: thick;
    display: 'flex';
    right: 0;
    left: 0;
    margin-right: auto;
    margin-left: auto;
    min-height: 10em;
    width: 60%;
  }

  .title {
    margin: auto;
    text-align: center;
    padding-top: 25px;
    font-size: 2em;
    font-weight: bold;
    font-family: 'Edu SA Beginner', serif;
    background-color: rgb(50, 50, 50);
    color: #fff; 
    padding-bottom: 2rem;
    margin-bottom: 45px;
  }
  
  
  @media screen and (min-width: 620px) 
  {
    .submit {
      text-align: center;
      font-family: 'Edu SA Beginner', serif;
      font-size: 1.15em;
      background-color: #FFFFFF;
      border-radius: 25px;
      padding: 15px;
      padding-bottom: 35px;
      border-style: solid;
      border-width: thick;
      display: 'flex';
      right: 0;
      left: 0;
      margin-right: auto;
      margin-left: auto;
      min-height: 10em;
      width: 30%;
    }
  }
  